<!--【首页底部区域】：包含底部说明-->
<template>
  <div class="footer">
    <div class="footer-content">
      <div class="tab-link">
        <div class="tab pointer" @click="toPage({name:'front'})">网站首页 <span class="sept">|</span></div>
        <div class="tab pointer" :to="{ path: '/front' }">隐私策略<span class="sept">|</span></div>
        <div class="tab pointer" :to="{ path: '/front' }">安全中心<span class="sept">|</span></div>
        <div class="tab pointer" :to="{ path: '/front' }">关于我们<span class="sept">|</span></div>
        <div class="tab pointer" :to="{ path: '/front' }">联系我们<span class="sept"></span></div>
      </div>
      <div class="clearfix"></div>
      <div class="pointer contact">
        <span class="el-icon-phone-outline phone">400-123456</span>
        <el-image class="official" src="/static/images/front/contact/wx_official.jpg"></el-image>
      </div>
    </div>
    <div class="copyright pointer">Copyright © 2022 by {{projectName}}</div>
  </div>
</template>

<script>
  export default {
    name: "footer",
    computed: {
      projectName() {
        return this.$store.getters.config.projectName
      },
    },
  }
</script>
<style scoped>
  .footer {
    background: #2b4a78;
    color: #b0b0b0;
  }

  .footer-content {
    margin: 0 auto;
    width: 1200px;
  }

  .contact {
    float: right;
    margin-right: 100px;
  }
  .sept {
    color: #b0b0b0;
    padding-left: 15px
  }

  .copyright {
    clear: both;
    padding-bottom: 10px;
    text-align: center;
    font-size: smaller;
    color:#b0b0b0;
  }

  .tab {
    padding: 10px;
    font-size: 15px;
    float: left;
    margin-left: 30px;
  }

  .tab:hover {
    color: white;
  }

  .tab-link {
    width: 800px;
    margin: 0 auto;
    padding: 10px
  }

  .phone {
    margin-right: 50px;
    color: #FF6700;
    font-size: 20px;
  }

  .official {
    vertical-align: middle;
    width: 60px;
    height: 60px
  }

</style>



