<template>
<!-- 要 picture、name(或者title)、id-->
    <div :key="item.id" :style="'width:'+(100/column).toFixed(2)+'%'" class="card-body">
      <el-card shadow="hover" class="pointer">
        <div style="height: 250px">
          <el-image class="card-img" :src="formatUrl(item.picture)"></el-image>
          <b class="font-overflow card-title">{{item.title==null?item.name:item.title}}</b>
          <div v-html="delHtmlTag(item.content)" class="content"></div>
        </div>
        <div class="card-footer">
          <span class="footer-time"><i class="el-icon-time"></i> {{item.createDate.split(' ')[0]}}</span>
          <el-button class="footer-btn" @click="toPage({name:pageName,query:{id:item.id}})" type="text"  size="mini">查看详情</el-button>
        </div>
      </el-card>
    </div>
</template>
<script>
    export default {
      props: {
        item: {
          type: Object,
          default: {picture:"",title:"",name:"",content:""}
        },
        pageName: {
          type: String,
          default: ""
        },
        column: {
          type: Number,
          default: 4
        },
      },
      methods: {
        delHtmlTag(str){
          return str.replace(/<[^>]+>/g,"");  //正则去掉所有的html标记
        },
      }
    };
</script>
<style scoped>
  .card-body {
    float: left;
    padding: 5px;
  }
  .content {
    font-size: 14px;
    color: #999;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  .card-img {
    border-radius: 5px;
    height: 150px;
    width: 100%
  }

  .card-title {
    display: block;
    padding: 10px 0;
    font-family: cursive
  }

  .card-footer {
    padding: 15px
  }

  .footer-time {
    float: left;
    font-size: 14px;
    color: #999;
    margin-top: 5px;
  }

  .footer-btn {
    float: right;
    margin-bottom: 5px
  }
</style>



